.wrapper {
  position: relative;
}

@media screen and (min-width: 1140px) {
  .wrapper {
    width: 1200px;
    margin: 0 auto;
  }
}

.header {
  padding: 20px 15px;
  border-bottom: 2px solid #333;
  color: #fff;
}

@media screen and (min-width: 1140px) {
  .header {
    padding: 25px 0;
    border-bottom: 3px solid #333;
  }
}

.return-shop {
  padding: 10px 20px;
  background-color: var(--orange);
  font-size: 1.65rem;
  border: none;
  border-radius: 2px;

  &:hover {
    cursor: pointer;
    opacity: 0.9;
  }
}

.detail-product {
  padding: 0 28px;
  margin: 30px 0;
}

.icon-left {
  padding-right: 4px;
}

//img
.img-product {
  padding: 0 20px;
}

@media screen and (min-width: 1140px) {
  .img-product {
    padding: 0;
  }
}

.img-product img {
  width: 85%;
}
.img-item {
  box-shadow: 0 0 0 0.2rem rgb(0 123 255 / 25%);
}

.img-other {
  display: flex;
  width: 85%;
}

.img-other img {
  margin-top: 10px;
  width: 100%;

  &:hover {
    cursor: pointer;
  }
}

.img-item:focus {
  box-shadow: 0 0 0 0.2rem rgb(0 123 255 / 25%);
}

//info

.info-item {
  display: flex;
  justify-content: space-between;
}

.info-item p {
  margin: 30px 0;
  font-size: 1.6rem;
}

.name-status {
  text-transform: capitalize;
}

.price {
  display: flex;
  justify-content: space-between;
}

.select {
  display: flex;
  justify-content: space-between;
}

.select div {
  width: 100%;
}

.size {
  padding-right: 10px;
}

.select-tag {
  height: 32px;
  width: 100%;
  padding: 4px 8px;
  margin-top: 4px;
  outline: none;
  border: 1px solid #ccc;
  border-radius: 2px;

  &:focus {
    box-shadow: 0 0 0 0.2rem rgb(0 123 255 / 25%);
  }
}

.quantity {
  height: 32px;
  width: 100%;
  padding-left: 10px;
}

.quantity input {
  height: 32px;
  width: 100%;
  margin-top: 4px;
  padding: 4px 8px;
  outline: none;
  border: 1px solid #ccc;
  border-radius: 2px;

  &:focus {
    box-shadow: 0 0 0 0.2rem rgb(0 123 255 / 25%);
  }
}

.action {
  display: flex;
  justify-content: space-between;
  margin: 30px 0 40px 0;
}

.add {
  padding: 15px 20px;
  width: 80%;
  font-size: 1.75rem;
  font-weight: 600;
  color: #fff;
  background-color: var(--orange);
  border: none;
  border-radius: 4px;
  margin-right: 20px;

  &:hover {
    cursor: pointer;
    opacity: 0.9;
  }
}

.icon-cart {
  padding-right: 12px;
}

.heart {
  padding: -5px;
  border: none;
  background-color: transparent;
  margin-right: 8px;

  &:hover {
    cursor: pointer;
  }
}

.toast {
  top: 10px;
  right: 20px;
  width: 20%;
  float: right;
  z-index: 2;
}

.add-success {
  padding: 10px;
  background: #07bc0c;
  color: #fff;
}

.add-err {
  padding: 10px 20px;
  background-color: #e74c3c;
  color: #fff;
}

.gender-info {
  text-transform: capitalize;
}

.sub-title {
  padding-bottom: 30px;
}

.sub-title h3 {
  color: var(--orange);
  padding-bottom: 20px;

  &:hover {
    cursor: pointer;
  }
}
